import { useCallback, useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Card, CardBody, CardHeader, Col, Container, Input, InputGroup, Row } from 'reactstrap';
import { TEMPERATURE_C, TEMPERATURE_F, TEMPERATURE_K } from '../../../units';
import { SET_CP, SET_HEAT_OF_REACTION, SET_PRESSURE, SET_TEMPERATURE, SET_TEMPERATURE_UNIT } from '../store';
import CpIcon from './icons/cp.png';
import HeatIcon from './icons/heat.png';
import PressureIcon from './icons/pressure.png';
import TemperatureIcon from './icons/temperature.png';

const ParamCard = ({ label, icon, unitList, name, unitKey, valueAction, unitAction }) => {
  const value = useSelector(state => state.operatingParams[name]);
  const unit = useSelector(state => state.operatingParams[unitKey]);
  const dispatch = useDispatch();

  useEffect(() => {
    if(!unit && unitAction) {
      dispatch(unitAction(unitList[0]));
    }
  }, [dispatch, unitAction, unitList, unit]);

  const onValueChange = useCallback((e) => {
    dispatch(valueAction(e.target.value));
  }, [dispatch, valueAction]);

  const onUnitChange = useCallback((e) => {
    if(unitAction) {
      dispatch(unitAction(e));
    }
  }, [dispatch, unitAction]);

  return (
    <Card>
      <CardHeader className={"fw-bolder " + (!value ? "bg-danger text-white" : "")}>
        {label}
      </CardHeader>
      <CardBody tag={Container}>
        <Row>
          <Col sm={1}>
            <img src={icon} width={32} />
          </Col>
          <Col>
            <InputGroup>
              <Input value={value} invalid={!value} onChange={onValueChange} placeholder={`Enter ${label} in ${unit || unitList[0]}`} />
                {unitList && unitList.map((e =>
                  (<Button color="dark" outline active={e === unit || unitList.length === 1} onClick={() => onUnitChange(e)}>{e}</Button>)
                ))}
            </InputGroup>
          </Col>
        </Row>
      </CardBody>
    </Card>
  );
};

export const TemperatureCard = () => {
  const props = {
    label: 'Temperature',
    icon: TemperatureIcon,
    name: 'temperature',
    unitKey: 'temperatureUnit',
    valueAction: SET_TEMPERATURE,
    unitAction: SET_TEMPERATURE_UNIT,
    unitList: [TEMPERATURE_C, TEMPERATURE_F, TEMPERATURE_K],
  };

  return <ParamCard {...props} />
};

