import _ from 'lodash';

export const TEMPERATURE_C = "C";
export const TEMPERATURE_F = "F";
export const TEMPERATURE_K = "K";

export const normalizer = {
    /**
     * @param {import('./pages/Tool/state').OperatingParams} operatingParams 
     */
    getNormalTemperature: (operatingParams) => {
        if (operatingParams.temperatureUnit === TEMPERATURE_F) {
            return (operatingParams.temperature - 32) * 0.56;
        }

        if (operatingParams.temperatureUnit === TEMPERATURE_K) {
            return operatingParams.temperature - 273.15;
        }

        return operatingParams.temperature;
    },

}

/**
 * @param {import('./pages/Tool/state').OperatingParams} operatingParams 
 */
export const normalizeUnits = (operatingParams) => {
    const retval = _.cloneDeep(operatingParams);

    retval.temperature = normalizer.getNormalTemperature(operatingParams);

    return retval;
};